$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

.page-template-page-about {
  .aboutIntroInner {
    text-align: center;
  }
  #nonProfit {
    background-color: $blue;
    .nonProfitInner {
      padding-top: 6rem;
      padding-bottom: 6rem;
      color: #fff;
    }
  }
  #ulpCommunity {
    a.memberLink {
      color: #000;
      &:hover, &:visited {
        color: #000;
        text-decoration: none;
      }
    }
    .commMemberContainer {
      margin-bottom: 2rem;
      .commMemberImgWrap {
        background-size: cover;
        background-position: center;
        width: 200px;
        height: 200px;
        border-radius: 50%;
        border: 2px solid $yellow;
        display: inline-block;
        margin: auto auto 1rem auto;
        img {
          border: 2px solid $white;
          border-radius: 50%;
          overflow: hidden;
        }
        &:hover {
          border-color: $blue;
        }
      }
    }
  }
}

.single-members {
  .memberContainer {
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
  .memberName {
    margin-bottom: 2rem;
  }
  .memberInfoList {
    li {
      text-transform: uppercase;
      font-size: 0.8rem;
      letter-spacing: 0.01rem;
      margin-bottom: 10px;
      span {
        color: #ddd;
      }
    }
  }
}